<template>
  <transition name="game">
  <div class="container" :class="styles" v-if="notStarted && hasProgress">
    <div class="row d-flex justify-content-center">
      <button class="gamebtn margintop" @click="clickStarted">
        Продолжить игру
      </button>
      <button class="gamebtn margintop" @click="restartGame">
        Начать новую игру
      </button>
      <button class="gamebtn margintop" @click="goToResPage" v-if="hasResult">
        Посмотреть результат
      </button>
    </div>
  </div>
  <div v-else-if="levelNotStarted">
    <level-component
        v-if="level===1"
        :level="level"
        name="ИГРАЕМ НА ПРИБЫЛЬ"
        desc1="были успешными"
        desc2="– вы получили прибыль $1 000 000"
        :start-money="startMoney[level]"
        :total="total[level]"
        :prev-profit="prevProfit[level]">
    </level-component>
    <level-component v-else-if="level===2"
                     :level="level"
                     name="ИГРАЕМ С НУЛЯ"
                     desc1=""
                     desc2="не принесли вам прибыль"
                     :start-money="startMoney[level]"
                     :total="total[level]"
                     :prev-profit="prevProfit[level]">
    </level-component>
    <level-component v-else-if="level===3"
                     :level="level"
                     name="ИГРАЕМ С УБЫТКОВ"
                     desc1="были неудачными"
                     desc2="– ваш убыток составляет $800 000"
                     :start-money="startMoney[level]"
                     :total="total[level]"
                     :prev-profit="prevProfit[level]">
    </level-component>
    <div class="container">
      <button class="cardbtn" @click="startLevel" >
        Начать кейс {{ level }}
      </button>
    </div>
  </div>
	<div class="container" :class="styles" v-else>
    <div class="row">
      <div class="col-lg-1 col-md-1"></div>
      <div class="col-lg-3 col-md-3 col-sm-12 col-md-12">
        <button class="gamebtn" @click="exit">
          Выход в меню
        </button>
      </div>
      <div class="col-lg-6 col-md-6 col-sm-12 col-md-12
                  d-lg-flex d-md-block d-sm-block d-block
                  justify-content-end">
        <button class="gamebtn" @click="restartLevel">
          Начать кейс заново
        </button>
      </div>
			<p v-if="level===1" class="font-24" style="margin-top: 10px;">КЕЙС {{ level }}. ИГРАЕМ НА ПРИБЫЛЬ</p>
			<p v-if="level===2" class="font-24" style="margin-top: 10px;">КЕЙС {{ level }}. ИГРАЕМ С НУЛЯ</p>
			<p v-if="level===3" class="font-24" style="margin-top: 10px;">КЕЙС {{ level }}. ИГРАЕМ С УБЫТКОВ</p>
			<div class="container">
				<div class="row">
            <div class="col-lg-1 col-md-1"></div>
            <div :class="col3">
              <p class="font-19" style="margin: 0">
                Сумма сделки:<br>
              </p>
              <p class="d-lg-block d-md-block d-sm-none d-none font-14"
                  style="margin: 0">
                  *можно инвестировать только<br>средства со счета
              </p>
              <input id="input" type="text" @keyup.enter="onEnter"
                     @copy.prevent @paste.prevent
                     @keypress="isNumber($event)"
                     placeholder="Введите сумму"
                     v-model="investing"
                     autocomplete="off">
              <button class="dealbtn" @click="onEnter">
                <span>OK</span>
              </button>
              <div :class="dFlexWrap">
                  <div v-for="usualInvest of usualInvests" :key="usualInvest.id">
                    <button class="choosebtn" @click="addInvest(usualInvest)"
                            :disabled="usualInvest > money || usualInvest + invest > money">
                      + {{(usualInvest).toLocaleString()}}
                    </button>
                  </div>
              </div>
					</div>
          <br :class="dNoneBlock">
					<div :class="col6">
            <div :class="dFlexBlock">
              <p style="margin-bottom: 0">Число сделок в уровне: {{ total[level] }}</p>
            </div>
            <div :class="dFlexBlock">
              <p class="font-19" style="margin-bottom: 0">Совершено сделок:
                <span class="font-28"
                    style="color: #6600CC; font-weight: bold;">
                  {{ cnt }}
                </span>
              </p>
            </div>
            <div class="d-lg-flex d-md-block d-sm-block d-block
                  justify-content-end" style="margin-top: 0">
              <p class="font-19"
                 style="margin-top: 0; margin-bottom: 0">
                Текущий размер счета:
                <span class="font-28"
                      style="color: #6600CC; font-weight: bold">
                  ${{ money.toLocaleString() }}
                </span>
                <br>
                <!--<span style="opacity: 0.5;"> (рекорд: ${{ record.toLocaleString() }}) </span>-->
              </p>
            </div>
            <br :class="dNoneBlock">
            <div class="graph">
              <Scatter :data="chartData" :options="chartOptions" :style="myStyles"/>
            </div>
					</div>
				</div>
			</div>
		</div>
	</div>
  </transition>
</template>

<script>
import { Scatter } from 'vue-chartjs'
import LevelComponent from '@/components/LevelComponent.vue'
import {
  Chart as ChartJS,
  LinearScale,
  PointElement,
  LineElement,
  Tooltip
} from 'chart.js'
import * as chConfig from './chartConfig.js'
import VueSimpleAlert from "vue3-simple-alert";
ChartJS.register(LinearScale, PointElement, LineElement, Tooltip)
export default {
	name: "GameView",
	components: { Scatter, LevelComponent },
	computed: {
    investing: {
      get () {
        return this.invest == null ? null : '$' + this.invest.toLocaleString()
      },
      set(value) {
        this.invest = +value.replace(/\D/g,'')
        this.invest = eval(value)
      }
    },
		chartData: { 
			get () { 
				return {
					ticks: [ { fontColor: '#FFFFFF' } ],
					datasets: [ {
						fill: false,
						borderColor: this.pointsColor,
						borderWidth: 5,
						backgroundColor: this.pointsColor,
						data: this.points
					} ] 
				}
			},
			set (value) {
				this.points = value['data']
				this.pointsColor = value['colors']
			}
		},
		chartOptions() {
      let options = JSON.parse(JSON.stringify(chConfig.options));
      options.plugins.legend.display = false
      options.plugins.title.display = false
      options.scales.y.title.text = 'Текущий счет, $'
      return chConfig.options
    },
		myStyles () { return chConfig.styles },
    dFlexBlock () {
      if (this.width < 992 && this.width >= 750) {
        return "d-lg-flex d-md-block d-sm-block d-block justify-content-end"
      }
      return "d-lg-flex d-md-flex d-sm-block d-block justify-content-end"
    },
    dNoneBlock () {
      if (this.width < 992 && this.width >= 750) {
        return "d-lg-none d-md-block d-sm-block d-block"
      }
      return "d-lg-none d-md-none d-sm-block d-block"
    },
    dFlexWrap () {
      if (this.width < 992 && this.width >= 750) {
        return "d-lg-block d-md-flex d-sm-flex d-flex justify-content-center flex-wrap"
      }
      return "d-lg-block d-md-block d-sm-flex d-flex justify-content-center flex-wrap"
    },
    col3 () {
      if (this.width < 992 && this.width >= 750) {
        return "col-lg-3 col-md-12"
      }
      return "col-lg-3 col-md-3"
    },
    col6 () {
      if (this.width < 992 && this.width >= 750) {
        return "col-lg-6 col-md-12"
      }
      return "col-lg-6 col-md-6"
    }
	},
	data () {
		return {
      usualInvests: [10000, 50000, 100000, 250000, 500000, 1000000, 2000000],
			notStarted: true,
      levelNotStarted: true,
			hasProgress: false,
			hasResult: false,
			styles: 'bkgOut',
			level: 1,
      startMoney: {
        1: 1000000,
        2: 1000000,
        3: 1000000,
      },
      prevProfit: {
        1: 1000000,
        2: 0,
        3: -800000,
      },
			total: {
        1: 15,
        2: 15,
        3: 15,
      },
			cnt: 0,
			record: 0,
			money: 2000000,
			invest: null,
			points: [
				{
					x: 0,
					y: 2000000
				}
			],
      pointsColor: ['#32CD32'],
			percent: 60,
      totalSum: {
        1: 0,
        2: 0,
        3: 0,
      },
      historyPoints: {
        1: [],
        2: [],
        3: [],
      },
      width: window.innerWidth,
		}
	},
	methods: {
    updateWidth() {
      this.width = window.innerWidth;
    },
    isNumber: function(evt) {
      evt = (evt) ? evt : window.event;
      let charCode = (evt.which) ? evt.which : evt.keyCode;
      if ((charCode > 31 && (charCode < 48 || charCode > 57))) {
        evt.preventDefault()
      } else {
        return true;
      }
    },
    addInvest(value) {
      this.invest += eval(value)
      if (window.innerWidth >= 1000) {
        document.getElementById('input').focus()
      }
    },
    alert(text, type='info') {
      VueSimpleAlert.alert('','',type, { confirmButtonColor: ' #6600CC', html: text})
    },
		clickStarted () {
			this.notStarted = false
			this.styles = 'bkgIn'
		},
		exit () {
			this.notStarted = true
      this.levelNotStarted = false
      this.hasProgress = true
			this.styles = 'bkgOut'
		},
		endGame () {
			let record = this.record
      let totalSum = this.totalSum
      let historyPoints = this.historyPoints
			localStorage.clear()
			this.record = record
      this.totalSum = totalSum
			this.hasProgress = false
			this.cnt = 0
			this.level = 1
			this.money = this.startMoney[this.level] + this.prevProfit[this.level]
			this.percent = 60
			this.invest = null
			let pointY = this.money
			this.chartData = {'data': [{x: 0, y: pointY}], 'colors': ['#32CD32']}
      this.historyPoints = historyPoints
		},
		restartGame () {
			this.endGame()
			this.clickStarted()
      this.levelNotStarted = true
		},
		restartLevel () {
			this.cnt = 0
			this.invest = null
			this.money = this.startMoney[this.level] + this.prevProfit[this.level]
			let pointY = this.money
			this.chartData = {'data': [{x: 0, y: pointY}], colors: ['#32CD32']}
      this.levelNotStarted = true
		},
    goToResPage() {
      localStorage.setItem("record", this.record)
      localStorage.setItem("totalSum", JSON.stringify(this.totalSum))
      localStorage.setItem("hasProgress", this.hasProgress)
      localStorage.setItem("hasResult", "true")
      localStorage.setItem("historyPoints", JSON.stringify(this.historyPoints))
      this.$router.push({ path: '/res'});
    },
    startLevel () {
      this.levelNotStarted = false
      this.clickStarted()
    },
    setResItems(){
      this.historyPoints[this.level-1] = this.chartData.datasets;
      localStorage.setItem("historyPoints", JSON.stringify(this.historyPoints))
      this.totalSum[this.level-1] = this.money
    },
    nextLevel () {
      this.setResItems()
      this.cnt = 0
      this.money = this.startMoney[this.level] + this.prevProfit[this.level]
      this.invest = null
      let pointY = this.money
      this.chartData = {'data': [{x: 0, y: pointY}], colors: ['#32CD32']}
      // this.percent = this.percent - 4
      this.levelNotStarted = true
    },
    endGameRoutine () {
      this.setResItems()
      this.endGame()
      this.exit()
      this.goToResPage()
    },
    deal () {
      let pointsColors = this.pointsColor
      if (100 * Math.random() < this.percent) {
        this.money = this.money + this.invest
        VueSimpleAlert.fire({
          title: "<h1 style='color:black'>Сделка прошла успешно!</h1>",
          showConfirmButton: false,
          width: 600,
          timer: 1500,
          padding: '3em',
          background: '#A8E4A0',
          backdrop: `
          rgba(0,0,123,0.4)
          url("` + require("../.././assets/fireworks.gif") + `")
          top
          no-repeat`
        })
        pointsColors = this.pointsColor.concat('#32CD32')
      }
      else {
        this.money = this.money - this.invest
        VueSimpleAlert.fire({
          title: "<h1 style='color:black'>Сделка прошла неудачно...</h1>",
          showConfirmButton: false,
          width: 600,
          timer: 1500,
          padding: '3em',
          background: '#FFA19B',
          backdrop: `
          rgba(0,0,123,0.4)
          url("` + require("../../assets/sad.gif") + `")
          top
          no-repeat`
        })
        pointsColors = this.pointsColor.concat('#FF0000')
      }
      let pointY = this.money
      let point = {
        x: this.cnt,
        y: pointY
      }
      let points = this.points.concat(point)

      this.invest = null
      this.chartData = {'data': points, 'colors': pointsColors }
      if (this.money <= 0) {
        this.level++
        if (this.level <= 3) {
          this.alert('К сожалению, вы проиграли все возможные средства. ' +
              '<br>Ваш счет: $' + this.money.toLocaleString())
          this.nextLevel()
        }
        else {
          this.alert('К сожалению, вы проиграли все возможные средства на финальном кейсе. ' +
              '<br>Ваш счет: $' + this.money.toLocaleString())
          this.endGameRoutine()
        }
      }
    },
		onEnter () {
      this.invest = eval(this.invest)
			this.cnt = this.cnt + 1
			if (!this.invest || this.invest <= 0) {
				this.alert('Размер одной суммы сделки должен быть больше нуля', 'warning')
				this.invest = null
				this.cnt = this.cnt - 1
			}
      else if (this.invest > this.money) {
        this.alert('Размер одной суммы сделки должен быть не больше текущего счета', 'warning')
        this.invest = null
        this.cnt = this.cnt - 1
      }
			else {
        if (this.invest === this.money) {
          !VueSimpleAlert.confirm(
              'При таком вложении вы можете потерять все средства. ' +
              'Вы уверены?', '', 'warning', { confirmButtonColor: ' #6600CC' }).then(() => {
            this.deal()
          }).catch(() => {
            this.invest = null
            this.cnt = this.cnt - 1
          });
        }
        else{
          this.deal()
        }
      }
      if (this.money >= Number.MAX_VALUE / 2) {
        this.alert('Поздравляем! Вы достигли максимального счета! ' +
            '<br>Ваш счет: $' + this.money.toLocaleString(), 'success')
        this.level++
        if (this.level <= 3) {
          this.nextLevel()
        }
        else {
          this.endGameRoutine()
        }
      }
      else if (this.cnt === this.total[this.level]) {
        this.deal()
        this.level = this.level + 1
        if (this.level > 3) {
          // this.alert('Поздравляем! Вы прошли все уровни. Ваш счет: $' + this.money)
          this.alert('Поздравляем! Вы успешно прошли финальный кейс! ' +
              '<br>Ваш счет: $' + this.money.toLocaleString(), 'success')
          this.endGameRoutine()
        }
        else {
          if (this.money > 0) {
            this.alert('Поздравляем! Вы успешно прошли кейс! ' +
                "<br>Ваш счет: $" + this.money.toLocaleString(), 'success')
            this.nextLevel()
          } else {
            this.alert('К сожалению, вы проиграли все возможные средства ' +
                    '<br>Ваш счет: $' + this.money.toLocaleString())
            this.nextLevel()
          }
        }
      }
		},
	},
	mounted() {
    window.addEventListener('resize', this.updateWidth);
		if (localStorage.money) {
			this.money = eval(localStorage.money)
		}
		if (localStorage.level) {
			this.level = eval(localStorage.level)
		}
		if (localStorage.record) {
			this.record = eval(localStorage.record)
		}
		if (localStorage.cnt) {
			this.cnt = eval(localStorage.cnt)
		}
		if (localStorage.points) {
			this.points = JSON.parse(localStorage.points)
		}
    if (localStorage.pointsColor) {
      this.pointsColor = JSON.parse(localStorage.pointsColor)
    }
		if (localStorage.invest) {
			this.invest = eval(localStorage.invest)
		}
		if (localStorage.hasProgress) {
			this.hasProgress = eval(localStorage.hasProgress)
		}
    if (localStorage.hasResult) {
			this.hasResult = eval(localStorage.hasResult)
		}
    if (localStorage.totalSum) {
      this.totalSum = JSON.parse(localStorage.totalSum)
    }
    if (localStorage.levelNotStarted) {
      this.levelNotStarted = eval(localStorage.levelNotStarted)
    }
    if (localStorage.historyPoints) {
      this.historyPoints = JSON.parse(localStorage.historyPoints)
    }
	},
	watch: {
		money(newMoney) {
			localStorage.money = eval(newMoney);
			if (newMoney > this.record) {
				this.record = eval(newMoney)
			}
			if (!this.hasProgress && newMoney !== this.startMoney[0] + this.prevProfit[0]) {
				this.hasProgress = true
			}
		},
		level(newLevel) {
			localStorage.level = eval(newLevel)
		},
		record(newRecord) {
			localStorage.record = eval(newRecord)
		},
		cnt(newCnt) {
			localStorage.cnt = eval(newCnt)
		},
		points(newPoints) {
			localStorage.points = JSON.stringify(newPoints)
		},
    pointsColor(newColors) {
      localStorage.pointsColor = JSON.stringify(newColors)
    },
		invest(newInvest) {
			localStorage.invest = eval(newInvest)
		},
		hasProgress(newValue) {
      localStorage.hasProgress = eval(newValue)
    },
    hasResult(newValue) {
      localStorage.hasResult = eval(newValue)
    },
    totalSum(newValue) {
      localStorage.totalSum = JSON.stringify(newValue)
    },
    levelNotStarted(newValue) {
      localStorage.levelNotStarted = eval(newValue)
    },
    historyPoints(newPoints) {
      localStorage.historyPoints = JSON.stringify(newPoints)
    },
	}
}
</script>

<style>
	.bkgOut {
		background-color: #CCCCCC;
		margin-top: 20px;
		height:  655px;
	}

	.bkgIn {
		background-color: #CCCCCC;
		margin-top: 20px;
		height:  655px;
		color: #000;
	}

	.game {
		background-color: #6600CC;
		height:  550px;
		width: 900px;
		margin-top:1rem;
		margin-left:35px;
	}

	.gamebtn {
		background-color:  #6600CC;
		color:  #ffffff;
    margin-top: 10px;
		width: 12rem;
		height:  3rem;
	}

	.gamebtn:hover {
		background: #336666;
		color: #3a7999;
  }

  .choosebtn {
    background-color:  #C0C0C0;
    color:  #000;
    margin-top: 10px;
    width: 12rem;
    height:  2rem;
  }

  .choosebtn:hover {
    background: #808080;
    color: #3a7999;
  }

  .dealbtn {
    background-color:  #C0C0C0;
    color:  #000;
    margin-top: 11px;
    width: 4rem;
    height:  3rem;
  }

  .dealbtn:hover {
    background: #808080;
    color: #3a7999;
  }

  .graph {
    /*box-shadow: 10px 7px 0px 0px rgba(0, 0, 0, .4);*/
	}

	.margintop {
		margin-top:10rem;
	}

  #input {
    width: 8rem; height:  3rem;
  }

	/*
	@media (max-width: 991px) {
		.graph {
			box-shadow: 10px 7px 0px 0px rgba(0, 0, 0, .4);
		}
	}
	*/

  @media (max-width: 1000px) {
    .choosebtn {
      background-color:  #C0C0C0;
      color:  #000;
      margin-top: 1px;
      width: 9rem;
      height:  2rem;
    }
    #input {
      width: 7rem; height:  3rem;
      font-size: 14px;
    }
    .dealbtn {
      background-color:  #C0C0C0;
      color:  #000;
      margin-top: 11px;
      width: 2rem;
      height:  3rem;
      font-size: 12px;
    }
    .bkgIn {
      background-color: #CCCCCC;
      margin-top: 20px;
      height:  955px;
      color: #000;
    }
  }

  .game-leave-active,
  .game-leave-active {
    transition: opacity 0.5s, background 0.3s ease, color 0.2s linear;
  }

  .game-enter-from,
  .game-leave-to {
    opacity: 0;
  }
</style>