<template>
  <HomeComponent
      title1=""
      :title2="'КЕЙС '+level.toString()+'. '+name"
      btn-text="Играть"
      btn-href="/game"
      :is-res-view="false"
      :is-game-view="true">
    <template #history>
      <div class="row mt-3">
        <div class="col-lg-8 col-md-8 col-sm-12 col-xs-12">
          <p class="font-24">
            Прошлые сделки {{ desc1 }}
            <span style="color: #6600CC; font-weight: bold">
              {{ desc2 }}
            </span>
          </p>
          <br class="d-lg-none d-md-none d-sm-block d-block">
          <h3>
            Текущий размер счета:
            <span class="font-32" style="color: #6600CC;">
            ${{ (startMoney+prevProfit).toLocaleString() }}
            </span>
          </h3>
        </div>
        <div class="col-4 d-lg-block d-md-block d-sm-none d-none mb-3">
          <img v-if="level==1"
               style="width: 140px; height: 100px; border: none"
               src="../assets/up.jpg" alt="" class="img-thumbnail float-end"/>
          <img v-if="level==3" src="../assets/down.jpg"
               style="width: 140px; height: 100px; border: none"
               alt="" class="img-thumbnail float-end"/>
        </div>
      </div>
      <br class="d-lg-none d-md-none d-sm-block d-block">
    </template>
    <template #facts>
      <p>
        <span class="font-20" style="color: #6600CC;">Количество сделок:</span>
        у вас есть
        <span class="font-20" style="color: #6600CC;">{{ total }}</span>
        сделок, чтобы увеличить свои активы. <br>
        <span class="font-20" style="color: #6600CC;">Инвестируемая сумма: </span>
        ${{ startMoney.toLocaleString() }}<br>
        <span class="font-20" style="color: #6600CC;">Прибыль от прошлых сделок: </span>
        ${{ prevProfit.toLocaleString() }}<br>
        <span class="font-20" style="color: #6600CC;">Максимальный размер одной сделки: </span>
        текущий размер счета (инвестируемая сумма и прибыль от прошлых сделок).<br>
      </p>
    </template>
  </HomeComponent>
</template>

<script>
import HomeComponent from '@/components/HomeComponent.vue'

export default {
  name: 'LevelComponent',
  components: {
    HomeComponent
  },
  props: {
    name: String,
    level: Number,
    desc1: String,
    desc2: String,
    startMoney: Number,
    total: Number,
    prevProfit: Number
  }
}
</script>

